    #gswpts_tables_container .ui.stackable.grid {\r\n        margin: 0;\r\n\r\n        .row {\r\n            width: 105% !important;\r\n        }\r\n    }\r\n\r\n    #manage_tables_paginate>div {\r\n        padding-top: 0 !important;\r\n    }\r\n\r\n    #spreadsheet_container .ui.stackable.grid {\r\n        margin: 0;\r\n\r\n        .row {\r\n            width: 105% !important;\r\n        }\r\n\r\n        #create_tables th,\r\n        #create_tables td {\r\n            vertical-align: middle;\r\n        }\r\n    }\r\n\r\n    #spreadsheet_container {\r\n        #create_tables_wrapper {\r\n            width: 100% !important;\r\n        }\r\n\r\n        #create_tables_paginate>div {\r\n            padding-top: 0 !important;\r\n        }\r\n\r\n        .dt-buttons {\r\n            display: flex;\r\n            justify-content: flex-end;\r\n            margin-top: 5px;\r\n\r\n            button {\r\n                font-size: 13px;\r\n                margin: 0 5px;\r\n            }\r\n        }\r\n\r\n        .filtering_input {\r\n            display: flex;\r\n            justify-content: space-between;\r\n            align-items: center;\r\n            margin: 15px 0 5px 0;\r\n\r\n            #create_tables_length {\r\n                margin-right: auto;\r\n            }\r\n\r\n            #create_tables_filter {\r\n                margin-left: auto;\r\n            }\r\n        }\r\n\r\n        .bottom_options {\r\n            display: flex;\r\n            justify-content: space-between;\r\n            align-items: center;\r\n            margin-top: 10px;\r\n\r\n            .dataTables_info {\r\n                padding-top: 0 !important;\r\n                margin-right: auto;\r\n            }\r\n\r\n            #create_tables_paginate {\r\n                margin-left: auto;\r\n                padding: 0 5px;\r\n            }\r\n\r\n            .pagination.menu {\r\n                padding: 0 !important;\r\n                font-size: 14px;\r\n                background: transparent !important;\r\n                display: flex;\r\n                justify-content: center;\r\n                align-items: center;\r\n                flex-wrap: wrap;\r\n                border: none;\r\n                margin: 0 5px;\r\n\r\n                &>* {\r\n                    padding: 9px 16px;\r\n                    border: 1px solid #a9a1a13d;\r\n                    -webkit-box-shadow: 1px 1px 0px 0px rgba(145, 145, 145, 0.48);\r\n                    box-shadow: 1px 1px 0px 0px rgba(145, 145, 145, 0.48);\r\n\r\n                    &:first-child {\r\n                        border-radius: 0.28571429rem 0px 0px 0.28571429rem;\r\n                    }\r\n\r\n                    &:last-child {\r\n                        border-radius: 0px 0.28571429rem 0.28571429rem 0px 0px;\r\n                    }\r\n\r\n                    font-weight: bold;\r\n                    color: rgba(56, 13, 13, 0.787);\r\n\r\n                    &:hover {\r\n                        background-color: rgba(0, 0, 0, 0.03);\r\n                    }\r\n                }\r\n\r\n                a {\r\n                    text-decoration: none;\r\n                }\r\n\r\n                .active {\r\n                    background-color: #b3b3b35c;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n.gswpts_create_table_container,\r\n.gswpts_tables_container {\r\n    @include mixin.datatable_semantic;\r\n    @include mixin.loader_style;\r\n\r\n    .gswpts_table_title {\r\n        display: none;\r\n\r\n        &.active {\r\n            display: block;\r\n        }\r\n    }\r\n\r\n    // Design the hidden popup\r\n    .modal_wrapper {\r\n        position: absolute;\r\n        width: 100%;\r\n        height: 100%;\r\n        top: 0;\r\n        left: 0;\r\n        background: none;\r\n        display: flex;\r\n        justify-content: center;\r\n        align-items: center;\r\n        transition: 0.3s ease-in-out;\r\n        z-index: 5;\r\n        pointer-events: none;\r\n        background: #0000004a;\r\n        opacity: 0;\r\n    }\r\n\r\n    .modal_wrapper.active {\r\n        pointer-events: all;\r\n        opacity: 1;\r\n    }\r\n\r\n    .modal_container {\r\n        background: white;\r\n        border-radius: 8px;\r\n        width: 90%;\r\n    }\r\n\r\n    .settings_container {\r\n        width: 100%;\r\n        z-index: 99;\r\n        position: relative;\r\n\r\n        .large_promo_close {\r\n            position: absolute;\r\n            right: 0px;\r\n            top: -60px;\r\n            cursor: pointer;\r\n        }\r\n    }\r\n\r\n    .settings_container {\r\n        /* colors */\r\n        $link-color: #2ecc40;\r\n        $blue: $link-color;\r\n        $background-color: #fff;\r\n        $red: #ff4a53;\r\n        $dark: #333;\r\n        $accent: $blue;\r\n        $accent-inactive: desaturate($accent, 85%);\r\n        $secondary: $accent-inactive;\r\n        /* tab setting */\r\n        $tab-count: 3;\r\n        $indicator-width: 50px;\r\n        $indicator-height: 4px;\r\n        /* breakpoints */\r\n        $breakpoints: (\r\n            medium: #{$tab-count * 250px},\r\n            small: #{$tab-count * 150px},\r\n        );\r\n    /* selectors relative to radio inputs */\r\n    $label-selector: \"~ ul > li\";\r\n    $slider-selector: \"~ .slider\";\r\n    $content-selector: \"~ .content > section\";\r\n\r\n    @mixin tabs($label-selector: $label-selector, $slider-selector: $slider-selector, $content-selector: $content-selector) {\r\n        @for $i from 1 through $tab-count {\r\n            &:nth-of-type(#{$i}):checked {\r\n                #{$label-selector}:nth-child(#{$i}) {\r\n                    @content;\r\n                }\r\n\r\n                #{$slider-selector} {\r\n                    transform: translateX(#{100% * ($i - 1)});\r\n                }\r\n\r\n                #{$content-selector}:nth-child(#{$i}) {\r\n                    display: block;\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    .tabs {\r\n        padding: 20px;\r\n        width: 100%;\r\n        background-color: #fff;\r\n        border-radius: 5px;\r\n        min-width: #{$tab-count * 60px};\r\n\r\n        input[name=\"tab-control\"] {\r\n            display: none;\r\n        }\r\n\r\n        .content section h2,\r\n        ul li label {\r\n            font-weight: bold;\r\n            font-size: 18px;\r\n            color: $accent;\r\n        }\r\n\r\n        ul {\r\n            list-style-type: none;\r\n            padding-left: 0;\r\n            display: flex;\r\n            flex-direction: row;\r\n            margin-bottom: 10px;\r\n            justify-content: space-between;\r\n            align-items: center;\r\n            flex-wrap: wrap;\r\n\r\n            li {\r\n                box-sizing: border-box;\r\n                flex: 1;\r\n                // width: #{100% / $tab-count};\r\n                width: calc(100% / $tab-count);\r\n                padding: 0 10px;\r\n                text-align: center;\r\n\r\n                label {\r\n                    transition: all 0.3s ease-in-out;\r\n                    color: $secondary;\r\n                    padding: 5px auto;\r\n                    overflow: hidden;\r\n                    text-overflow: ellipsis;\r\n                    display: block;\r\n                    cursor: pointer;\r\n                    transition: all 0.2s ease-in-out;\r\n                    white-space: nowrap;\r\n                    -webkit-touch-callout: none;\r\n\r\n                    br {\r\n                        display: none;\r\n                    }\r\n\r\n                    svg {\r\n                        fill: $secondary;\r\n                        height: 1.2em;\r\n                        vertical-align: bottom;\r\n                        margin-right: 0.2em;\r\n                        transition: all 0.2s ease-in-out;\r\n                    }\r\n\r\n                    &:hover,\r\n                    &:focus,\r\n                    &:active {\r\n                        outline: 0;\r\n                        color: lighten($secondary, 15%);\r\n\r\n                        svg {\r\n                            fill: lighten($secondary, 15%);\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n        }\r\n\r\n        .slider {\r\n            position: relative;\r\n            // width: #{100% / $tab-count};\r\n            width: calc(100% / $tab-count);\r\n            transition: all 0.33s cubic-bezier(0.38, 0.8, 0.32, 1.07);\r\n\r\n            .indicator {\r\n                position: relative;\r\n                width: $indicator-width;\r\n                max-width: 100%;\r\n                margin: 0 auto;\r\n                height: $indicator-height;\r\n                background: $accent;\r\n                border-radius: 1px;\r\n            }\r\n        }\r\n\r\n        .content {\r\n            margin-top: 25px;\r\n\r\n            section {\r\n                display: none;\r\n\r\n                animation: {\r\n                    name: content;\r\n                    direction: normal;\r\n                    duration: 0.3s;\r\n                    timing-function: ease-in-out;\r\n                    iteration-count: 1;\r\n                }\r\n\r\n                line-height: 1.4;\r\n\r\n                h2 {\r\n                    color: $accent;\r\n                    display: none;\r\n\r\n                    &::after {\r\n                        content: \"\";\r\n                        position: relative;\r\n                        display: block;\r\n                        width: 30px;\r\n                        height: 3px;\r\n                        background: $accent;\r\n                        margin-top: 5px;\r\n                        left: 1px;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n\r\n        input[name=\"tab-control\"] {\r\n            @include tabs {\r\n                >label {\r\n                    cursor: default;\r\n                    color: $accent;\r\n\r\n                    svg {\r\n                        fill: $accent;\r\n                    }\r\n\r\n                    @media (max-width: map-get($breakpoints, small)) {\r\n                        background: rgba(0, 0, 0, 0.08);\r\n                    }\r\n                }\r\n            }\r\n        }\r\n\r\n        @keyframes content {\r\n            from {\r\n                opacity: 0;\r\n                transform: translateY(5%);\r\n            }\r\n\r\n            to {\r\n                opacity: 1;\r\n                transform: translateY(0%);\r\n            }\r\n        }\r\n\r\n        @media (max-width: map-get($breakpoints, medium)) {\r\n            ul li label {\r\n                white-space: initial;\r\n\r\n                br {\r\n                    display: initial;\r\n                }\r\n\r\n                svg {\r\n                    height: 1.5em;\r\n                }\r\n            }\r\n        }\r\n\r\n        @media (max-width: map-get($breakpoints, small)) {\r\n            ul li label {\r\n                padding: 5px;\r\n                border-radius: 5px;\r\n            }\r\n\r\n            ul {\r\n                flex-direction: column;\r\n                justify-content: center;\r\n                align-items: center;\r\n\r\n                li {\r\n                    width: 90%;\r\n                }\r\n            }\r\n\r\n            .slider {\r\n                display: none;\r\n            }\r\n\r\n            .content {\r\n                margin-top: 20px;\r\n\r\n                section h2 {\r\n                    display: block;\r\n                }\r\n            }\r\n        }\r\n\r\n        .display_settings,\r\n        .sort_filter,\r\n        .table_tools {\r\n            .feature-container {\r\n                display: grid;\r\n                grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));\r\n                grid-gap: 20px;\r\n\r\n                @media screen and (max-width: 512px) {\r\n                    grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));\r\n                }\r\n            }\r\n\r\n            .card {\r\n                position: relative;\r\n                width: 100%;\r\n                height: 60px;\r\n                display: flex;\r\n                justify-content: center;\r\n                align-items: center;\r\n            }\r\n\r\n            .content {\r\n                position: relative;\r\n                display: flex;\r\n                justify-content: center;\r\n                align-items: center;\r\n                margin: 0;\r\n                width: 100%;\r\n\r\n                .ui.dropdown {\r\n                    width: 250px !important;\r\n\r\n                    .ui.label {\r\n                        background-color: $link-color;\r\n                        color: #fff;\r\n                    }\r\n                }\r\n\r\n                .pro_feature {\r\n                    float: right;\r\n                    margin-top: -10px;\r\n                    font-size: 20px;\r\n                    color: $link-color;\r\n                }\r\n\r\n                .selectbox_overlay {\r\n                    position: absolute;\r\n                    width: 90%;\r\n                    margin: 0 auto;\r\n                    height: 40px;\r\n                    bottom: 15px;\r\n                    z-index: 20;\r\n                }\r\n\r\n                .card-top-header {\r\n                    text-transform: capitalize;\r\n                    display: flex;\r\n                    justify-content: space-between;\r\n                    align-items: center;\r\n                    width: 100%;\r\n\r\n                    &>span {\r\n                        font-size: 1.2rem;\r\n                        font-weight: bolder;\r\n                        margin-right: 5px;\r\n                        display: flex;\r\n                        justify-content: center;\r\n                        align-items: center;\r\n\r\n                        div {\r\n                            background: none;\r\n                            margin-left: 5px !important;\r\n                        }\r\n                    }\r\n\r\n                    .input-tooltip {\r\n                        position: relative;\r\n                        margin-right: 10px;\r\n\r\n                        svg {\r\n                            cursor: pointer;\r\n                        }\r\n\r\n                        .tooltiptext {\r\n                            opacity: 0;\r\n                            pointer-events: none;\r\n                            background-color: #000;\r\n                            color: #fff;\r\n                            text-align: center;\r\n                            border-radius: 6px;\r\n                            padding: 5px;\r\n                            position: absolute;\r\n                            z-index: 50;\r\n                            top: 130%;\r\n                            left: 0%;\r\n                            transform: translateX(-30%);\r\n                            min-height: 200px;\r\n                            transition: opacity 500ms ease-in-out;\r\n\r\n                            &::after {\r\n                                content: \"\";\r\n                                position: absolute;\r\n                                top: -15px;\r\n                                right: 65%;\r\n                                border-width: 8px;\r\n                                border-style: solid;\r\n                                border-color: transparent transparent #000 transparent;\r\n                            }\r\n\r\n                            span {\r\n                                display: block;\r\n                                margin-bottom: 8px;\r\n                                font-style: italic;\r\n                                padding: 2px;\r\n                            }\r\n\r\n                            img {\r\n                                max-width: 500px;\r\n                                min-width: 350px;\r\n                                width: 100%;\r\n                            }\r\n\r\n                            @media screen and (max-width: 600px) {\r\n                                left: -48px;\r\n                                top: 100%;\r\n                                margin-top: 15px;\r\n\r\n                                &::after {\r\n                                    display: none;\r\n                                }\r\n                            }\r\n                        }\r\n\r\n                        &:hover .tooltiptext {\r\n                            opacity: 1;\r\n                            pointer-events: all;\r\n                        }\r\n                    }\r\n                }\r\n\r\n                .tableStyle {\r\n                    display: flex;\r\n                    justify-content: center;\r\n                    flex-wrap: wrap;\r\n                    align-items: center;\r\n\r\n                    .label {\r\n                        margin-left: 20px;\r\n                    }\r\n\r\n                    .chooseStyle {\r\n                        cursor: pointer;\r\n                    }\r\n                }\r\n            }\r\n\r\n            .ui.toggle.checkbox {\r\n                display: flex;\r\n                justify-content: center;\r\n                flex-direction: column;\r\n                align-items: center;\r\n                margin-right: -10px;\r\n\r\n                input {\r\n                    visibility: hidden;\r\n                }\r\n\r\n                label {\r\n                    cursor: pointer;\r\n                    margin: 0 !important;\r\n                }\r\n\r\n                input:checked~.box:before,\r\n                input:checked~label:before {\r\n                    background-color: $link-color !important;\r\n                }\r\n            }\r\n\r\n            .pro_feature_promo {\r\n                position: absolute;\r\n                width: 0;\r\n                height: 0;\r\n                z-index: 20;\r\n                background: #0000007d;\r\n                overflow: hidden;\r\n                transition: all 0.1s ease-in-out;\r\n\r\n                a {\r\n                    margin: 0;\r\n                    position: absolute;\r\n                    top: 65%;\r\n                    left: 50%;\r\n                    transform: translateX(-50%);\r\n                }\r\n\r\n                .promo_close_btn {\r\n                    position: absolute;\r\n                    top: 0;\r\n                    left: 0;\r\n                    margin-left: 8px;\r\n                    margin: 8px 0 0 8px;\r\n                    color: white;\r\n                    font-size: 17px;\r\n                    cursor: pointer;\r\n                }\r\n\r\n                strong {\r\n                    position: absolute;\r\n                    top: 45%;\r\n                    left: 50%;\r\n                    transform: translate(-50%, -50%);\r\n                    white-space: nowrap;\r\n                    color: white;\r\n                    font-size: 15px;\r\n                }\r\n\r\n                &.active {\r\n                    width: 100%;\r\n                    height: 100%;\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    .disabled_checkbox *,\r\n    .disabled_checkbox>label {\r\n        visibility: hidden;\r\n        pointer-events: all;\r\n    }\r\n}\r\n}","@mixin button_style {\r\n    .ui.button {\r\n        cursor: pointer;\r\n        display: inline-block;\r\n        min-height: 1em;\r\n        outline: none;\r\n        border: none;\r\n        vertical-align: baseline;\r\n        background: #e0e1e2 none;\r\n        color: rgba(0, 0, 0, 0.6);\r\n        font-family: \"Lato\", \"Helvetica Neue\", Arial, Helvetica, sans-serif;\